.slider {
   padding: 20px 20px 0 20px;
   &__title {
      padding-bottom: 10px;
   }
}
#mySwiper {
   background: transparent;
}
.swiper {
   padding: 20px;
   width: 100%;
   height: 500px;
   border-radius: 25px;
}

.swiper-slide {
   text-align: center;
   font-size: 18px;
   display: flex;
   justify-content: center;
   align-items: center;
   border-radius: 25px;
}
.swiper-slide img {
   display: block;
   width: 100%;
   height: 100%;
   object-fit: cover;
   border-radius: 25px;
   user-select: none;
}
.swiper-button-prev,
.swiper-button-next {
   background: #27272780;
   border-radius: 25px;
   padding: 28px 15px 25px;
}
@media (max-width: $mobile) {
   .swiper {
      height: auto;
      aspect-ratio: 5/ 4;
   }
}
