/* Stilizează punctele de paginare */
.pagination {
   display: flex;
   list-style: none;
   padding: 0;
   justify-content: center;
   align-items: center;
   gap: 10px;
   margin-top: 20px;
   padding: 10px 20px;
}

.pagination li {
   height: 40px;
   width: 40px;
   display: inline-block;
   cursor: pointer;
   font-size: 16px;
   display: flex;
   align-items: center;
   justify-content: center;
   a {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
   }
}

.pagination .active {
   font-weight: bold;
   color: #007bff; /* Culoarea activă */
}

.pagination li:hover {
   text-decoration: underline;
   color: #007bff; /* Culoarea la hover */
}
.previous,
.next {
   height: 40px;
   width: 60px;
   a {
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
   }
   .paginate-icon {
      min-width: 40px;
      min-height: 40px;
   }
   background: var(--black-t);
   border-radius: 20px;
}
